import { createClient } from '../plugins/Contentful.js'
const client = createClient()

export default {
  data() {
    return {
      blogs: [],
      readMore: false
    }
  },

  asyncData({ env }) {
    return Promise.all([
      client.getEntries({
        content_type: env.CTF_BLOG_POST_TYPE_ID,
        order: '-sys.createdAt',
      }),
    ])
      .then(([blogs]) => {
        return {
          blogs: blogs.items,
        }
      })
      .catch(console.error)
  },
}
